import React from "react";
import "./Detail.css";

class Detail extends React.Component{
    componentDidMount(){
        console.log(this.props)
        const {location, history} = this.props;
        if(location.state ===undefined){
            history.push("/")
        }
    }
    render(){
        const {location} = this.props;
        console.log(location)
            if(location.state) {
                return (
                <div className="movie_detail">
                    <div className="imgBox">
                        <img className="movie_poster" src={location.state.poster} alt={location.state.title}/>
                    </div>
                    <div className="movie_data">
                        <h3 className="movie_title">{location.state.title}</h3>
                        <h5 className="movie_year">{location.state.year}</h5>
                        <ul className="movie_genres">{location.state.genres.map((genres, index) => <li key={index} className="genres_genre">{genres}</li>)}</ul>
                        <p className="movie_summary">{location.state.summary}</p>
                    </div>
                </div>
                )}
            else{
                return null;
            }
        
    }
}
export default Detail;